body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #553055;
}

.App{
  max-width: 400px;
  margin: 10px auto;
  text-align: center;
}

.navbar{
  color: white;
  background-color: #6d3d6d;
  padding:10px 20px;
}

.book-list ul{
  padding: 0px;
  list-style-type: none;
}

li{
  background-color: #6d3d6d;
  padding: 7px;
  margin: 10px 0;
  border-radius: 5px;
  cursor: pointer;
}

li:hover{
  opacity: 0.7;
  text-decoration: line-through;
}

.title{
  color: white;
  font-weight: bold;
  font-size: 1.2em;
}

.author{
  font-size: 0.9em;
}

.empty{
  margin: 20px;
  text-align: center;
  color: crimson;
}
form{
  padding: 15px;
}
input[type="text"]{
  width: 100%;
  /* box-sizing: border-box; */
  background:#3c1f3c;
  color: white;
  border: 0;
  margin: 6px 0;
  padding: 8px;
}